import { useState, useContext } from "react"
import { v4 as uuidv4 } from 'uuid';
import { Box, Grid, ResponsiveContext, Text } from "grommet";
import { CenterBox, CenteredContainer } from "styles/styles";
import AddRecipeBigScreen from "./BigScreen";
import AddRecipeSmallScreen from "./SmallScreen";
import Selectors from "./components/Selectors";
import NewRecipeName from "./components/NewRecipeName";
import AddFileForm from "./components/AddFileForm";

import IngredientsTable from "./components/IngredientsForm";
import Instructions from "./components/Instructions";
import ResetSubmit from "./components/ResetSubmit";
import { useNavigate } from "react-router-dom";


function randomImgNameGenerator() {
  return uuidv4(); // Generate random UUID
}

const AddRecipePage = ({ handleRecipeAdd, handleImageAdd }) => {

  const [newReceptName, setNewReceptName] = useState('')
  const [ingredients, setIngredients] = useState([])
  const [instructions, setInstructions] = useState("")
  const [recipespeed, setRecipeSpeed] = useState('')
  const [diets, setDiets] = useState([])
  const [category, setCategory] = useState('')
  const [mainIngredient, setMainIngredient] = useState('')
  const [file, setFile] = useState(null);
  const [imageSrc, setImageSrc] = useState('')

  const handleReceptName = (event) => setNewReceptName(event.target.value)
  const handleReceptSpeed = (event) => setRecipeSpeed(event.target.value)
  const navigate = useNavigate()

  const screenSize = useContext(ResponsiveContext);
  const isSmallScreen = ['xsmall', 'small'].includes(screenSize);

  const handleReset = (event) => {

    event.preventDefault()

    setNewReceptName('')
    setIngredients([])
    setInstructions('')
    setRecipeSpeed('')
    setDiets([])
    setCategory('')
    setMainIngredient('')
    setFile(null)
    setImageSrc('')
  }

  const handleSubmit = (event) => {
    event.preventDefault()

    const awsImageName = randomImgNameGenerator()

    handleImageAdd({ file, imageSrc, awsImageName })

    handleRecipeAdd({
      recipe_name: newReceptName,
      ingredients: ingredients,
      instructions: instructions,
      speed: recipespeed,
      category: category,
      main_ingredient: mainIngredient,
      diet: diets,
      imageName: awsImageName
    })

    setNewReceptName('')
    setIngredients([])
    setInstructions('')
    setRecipeSpeed('')
    setDiets([])
    setCategory('')
    setMainIngredient('')
    setFile(null)
    setImageSrc('')

    navigate('/')
  }

  return (
    <CenteredContainer>
      <CenterBox >
      {isSmallScreen 
      ? ( <AddRecipeSmallScreen {...{
        handleReceptName, handleReceptSpeed, handleReset, handleSubmit, newReceptName, setInstructions, instructions, recipespeed, 
        setCategory, diets, setDiets, category, ingredients, mainIngredient, setMainIngredient, file, setFile, setImageSrc, imageSrc, isSmallScreen
      }} /> 
      )
      : ( <AddRecipeBigScreen {...{
            handleReceptName, handleReceptSpeed, handleReset, handleSubmit, newReceptName, setInstructions, instructions, recipespeed, 
            setCategory, diets, setDiets, category, ingredients, mainIngredient, setMainIngredient, file, setFile, setImageSrc, imageSrc, isSmallScreen
          }} /> )
      }
      </CenterBox >
    </CenteredContainer >
  )
}

export default AddRecipePage